header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.header__container > h5 {
    font-weight: 100;
    font-size: 30px;
}

.header__container > h1 {
    font-weight: 600;
    font-size: 70px;
}

.header__container > p {
    width: 25rem;
    height: 10rem;
    font-size: 15px;
    position: absolute;
    left: calc(50% - 13rem);
    overflow: hidden;
    
}
/*=============================CTA=====================*/

.cta {
    margin-top: 10rem;
    display: flex;
    gap: 1rem;
    justify-content: center;
}

/*========================SOCIALS=====================*/

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);

}

/*=========================SCROLL DOWN====================*/

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 400;
    font-size: 0.9rem;
}

/* ================MEDIA QUERIES (MEDIUM DEVICES)======================*/

@media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
}

/* ================MEDIA QUERIES (SMALL DEVICES)======================*/

@media screen and (max-width: 600px) {
    header {
        height: 60%;
    }

    .header__container > p {
        width: 20rem;
        left: calc(50% - 10rem);
    }

    .header__socials,
    .scroll__down {
        display: none;
    }
}